export default ()=> {
      return (
        <div className="question" data-index="86" dangerouslySetInnerHTML = {{ __html: `
<h6>86. 哪个选项是将<code>hasName</code>设置为<code>true</code>的方法，前提是不能将<code>true</code>作为参数传递?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-function">function</span> <span class="token function">getName</span><span class="token punctuation">(</span><span class="token parameter">name</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
  <span class="token keyword keyword-const">const</span> hasName <span class="token operator">=</span> <span class="token comment">//</span>
<span class="token punctuation">}</span>
</code></pre>
<ul>
<li>A: <code>!!name</code></li>
<li>B: <code>name</code></li>
<li>C: <code>new Boolean(name)</code></li>
<li>D: <code>name.length</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: A</h4>
<p>使用逻辑非运算符<code>!</code>，将返回一个布尔值，使用<code>!! name</code>，我们可以确定<code>name</code>的值是真的还是假的。 如果<code>name</code>是真实的，那么<code>!name</code>返回<code>false</code>。 <code>!false</code>返回<code>true</code>。</p>
<p>通过将<code>hasName</code>设置为<code>name</code>，可以将<code>hasName</code>设置为等于传递给<code>getName</code>函数的值，而不是布尔值<code>true</code>。</p>
<p><code>new Boolean（true）</code>返回一个对象包装器，而不是布尔值本身。</p>
<p><code>name.length</code>返回传递的参数的长度，而不是布尔值<code>true</code>。</p>
</p>
</details>
` }}></div>
      );
    }